.hero {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  padding: 80px 20px;
  background: linear-gradient(135deg, #051821 0%, #265359 50%, #4A8FA6 100%);
}

.hero-content {
  position: relative;
  z-index: 10;
  max-width: 900px;
  text-align: center;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 40px;
  align-items: center;
}

.hero-profile {
  position: relative;
  width: 220px;
  height: 220px;
  border-radius: 50%;
  overflow: hidden;
  border: 6px solid #FF8C00;
  box-shadow: 0 20px 60px rgba(255, 140, 0, 0.3);
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.hero-text h1 {
  font-size: 64px;
  font-weight: 900;
  margin-bottom: 20px;
  line-height: 1.2;
  color: white;
}

.gradient-text {
  background: linear-gradient(135deg, #FF8C00 0%, #FFA500 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  display: inline-block;
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}

.subtitle {
  font-size: 28px;
  color: #FFA500;
  margin-bottom: 15px;
  font-weight: 700;
}

.description {
  font-size: 18px;
  color: #E0E0E0;
  margin-bottom: 40px;
  line-height: 1.6;
}

.hero-cta {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-bottom: 60px;
  flex-wrap: wrap;
}

.btn {
  padding: 15px 40px;
  border: none;
  border-radius: 50px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-block;
}

.btn-primary {
  background: linear-gradient(135deg, #FF8C00 0%, #FFA500 100%);
  color: white;
  box-shadow: 0 5px 15px rgba(255, 140, 0, 0.4);
}

.btn-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 30px rgba(255, 140, 0, 0.5);
}

.btn-secondary {
  background: transparent;
  color: #FFA500;
  border: 2px solid #FFA500;
}

.btn-secondary:hover {
  background: #FFA500;
  color: #051821;
}

.scroll-indicator {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  color: #FFA500;
  font-size: 24px;
  z-index: 10;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  overflow: hidden;
}

.blob {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.3;
  animation: blobMove 15s ease-in-out infinite;
}

.blob-1 {
  width: 300px;
  height: 300px;
  background: rgba(255, 140, 0, 0.3);
  top: -50px;
  right: -50px;
  animation-delay: 0s;
}

.blob-2 {
  width: 250px;
  height: 250px;
  background: rgba(74, 143, 166, 0.3);
  bottom: 50px;
  left: 50px;
  animation-delay: 2s;
}

.blob-3 {
  width: 280px;
  height: 280px;
  background: rgba(255, 165, 0, 0.2);
  top: 50%;
  left: 50%;
  animation-delay: 4s;
}

@keyframes blobMove {
  0%, 100% {
    transform: translate(0, 0) scale(1);
  }
  25% {
    transform: translate(20px, -50px) scale(1.1);
  }
  50% {
    transform: translate(-20px, 20px) scale(0.9);
  }
  75% {
    transform: translate(50px, 50px) scale(1.05);
  }
}

@media (max-width: 768px) {
  .hero {
    min-height: 80vh;
    padding: 60px 20px;
  }

  .hero-profile {
    width: 160px;
    height: 160px;
    border-width: 5px;
  }

  .hero-text h1 {
    font-size: 48px;
  }

  .subtitle {
    font-size: 20px;
  }

  .description {
    font-size: 16px;
  }

  .hero-cta {
    flex-direction: column;
    align-items: center;
  }

  .btn {
    width: 100%;
    max-width: 300px;
  }
}

@media (max-width: 480px) {
  .hero {
    min-height: 70vh;
    padding: 40px 15px;
  }

  .hero-profile {
    width: 140px;
    height: 140px;
    border-width: 4px;
  }

  .hero-text h1 {
    font-size: 36px;
  }

  .subtitle {
    font-size: 18px;
  }

  .description {
    font-size: 14px;
  }
}
