.about {
  padding: 100px 20px;
  background: linear-gradient(135deg, #051821 0%, #0F3B4D 50%, #1A5F6F 100%);
  position: relative;
  overflow: hidden;
}

.about h2 {
  font-size: 48px;
  text-align: center;
  margin-bottom: 60px;
  background: linear-gradient(135deg, #FF8C00 0%, #FFA500 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.about-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  margin-bottom: 80px;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}

.about-card {
  background: linear-gradient(135deg, #1A4645 0%, #265359 100%);
  padding: 40px;
  border-radius: 20px;
  border-left: 4px solid #FF8C00;
  transition: all 0.3s ease;
  box-shadow: 0 10px 30px rgba(255, 140, 0, 0.1);
}

.about-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(255, 140, 0, 0.2);
}

.about-card h3 {
  font-size: 24px;
  margin-bottom: 15px;
  color: #FF8C00;
  font-weight: 700;
}

.about-card p {
  font-size: 16px;
  color: #E0E0E0;
  line-height: 1.8;
}

.section-subtitle {
  font-size: 32px;
  text-align: center;
  margin-bottom: 50px;
  color: white;
  font-weight: 700;
}

.interests-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 30px;
  margin-bottom: 80px;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}

.interest-card {
  background: rgba(26, 70, 69, 0.5);
  border: 2px solid #FF8C00;
  padding: 30px 20px;
  border-radius: 15px;
  text-align: center;
  transition: all 0.3s ease;
  cursor: pointer;
  backdrop-filter: blur(10px);
}

.interest-card:hover {
  background: rgba(255, 140, 0, 0.1);
  box-shadow: 0 10px 30px rgba(255, 140, 0, 0.2);
}

.interest-icon {
  font-size: 48px;
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
}

.interest-card p {
  font-size: 16px;
  font-weight: 600;
  color: white;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 30px;
  max-width: 800px;
  margin: 0 auto;
}

.stat {
  text-align: center;
  padding: 30px;
  background: linear-gradient(135deg, #FF8C00 0%, #FFA500 100%);
  color: #051821;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(255, 140, 0, 0.3);
  font-weight: 700;
}

.stat h4 {
  font-size: 24px;
  margin-bottom: 10px;
  color: #051821;
}

.stat p {
  font-size: 14px;
  opacity: 0.9;
  color: #051821;
}

@media (max-width: 768px) {
  .about {
    padding: 60px 20px;
  }

  .about h2 {
    font-size: 36px;
    margin-bottom: 40px;
  }

  .about-grid {
    grid-template-columns: 1fr;
    gap: 30px;
    margin-bottom: 60px;
  }

  .interests-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }

  .stats-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .about h2 {
    font-size: 28px;
  }

  .about-card {
    padding: 25px;
  }

  .section-subtitle {
    font-size: 24px;
  }

  .interest-card {
    padding: 20px 15px;
  }

  .interest-icon {
    font-size: 36px;
  }
}
