.skills {
  padding: 100px 20px;
  background: linear-gradient(135deg, #051821 0%, #0F3B4D 50%, #1A5F6F 100%);
  position: relative;
  overflow: hidden;
}

.skills h2 {
  font-size: 48px;
  text-align: center;
  margin-bottom: 60px;
  background: linear-gradient(135deg, #FF8C00 0%, #FFA500 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
}

.skill-category {
  background: linear-gradient(135deg, #1A4645 0%, #265359 100%);
  padding: 30px;
  border-radius: 20px;
  border-left: 4px solid #FF8C00;
  transition: all 0.3s ease;
  box-shadow: 0 10px 30px rgba(255, 140, 0, 0.1);
}

.skill-category:hover {
  box-shadow: 0 20px 50px rgba(255, 140, 0, 0.2);
  transform: translateY(-5px);
}

.skill-category h3 {
  font-size: 22px;
  margin-bottom: 20px;
  font-weight: 700;
  color: #FF8C00;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.skill-item {
  background: transparent;
  padding: 10px 16px;
  border-radius: 25px;
  font-size: 14px;
  font-weight: 600;
  border: 2px solid #FF8C00;
  color: #FFA500;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-block;
}

.skill-item:hover {
  transform: translateY(-3px);
  background: #FF8C00;
  color: #051821;
  box-shadow: 0 8px 16px rgba(255, 140, 0, 0.3);
}

@media (max-width: 768px) {
  .skills {
    padding: 60px 20px;
  }

  .skills h2 {
    font-size: 36px;
    margin-bottom: 40px;
  }

  .skills-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }

  .skill-category {
    padding: 20px;
  }

  .skill-category h3 {
    font-size: 18px;
    margin-bottom: 15px;
  }

  .skill-item {
    font-size: 12px;
    padding: 8px 12px;
  }
}

@media (max-width: 480px) {
  .skills h2 {
    font-size: 28px;
  }

  .skills-grid {
    grid-template-columns: 1fr;
  }

  .skill-category {
    padding: 15px;
  }

  .skill-category h3 {
    font-size: 16px;
  }
}
