.projects {
  padding: 100px 20px;
  background: linear-gradient(135deg, #051821 0%, #0F3B4D 50%, #1A5F6F 100%);
  position: relative;
  overflow: hidden;
}

.projects h2 {
  font-size: 48px;
  text-align: center;
  margin-bottom: 60px;
  background: linear-gradient(135deg, #FF8C00 0%, #FFA500 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
}

.project-card {
  background: linear-gradient(135deg, #1A4645 0%, #265359 100%);
  border-radius: 20px;
  padding: 40px;
  box-shadow: 0 10px 30px rgba(255, 140, 0, 0.1);
  border: 2px solid #FF8C00;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  height: 100%;
  cursor: pointer;
}

.project-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 50px rgba(255, 140, 0, 0.3);
}

.project-header {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
}

.project-icon {
  width: 60px;
  height: 60px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  flex-shrink: 0;
  background: linear-gradient(135deg, #FF8C00 0%, #FFA500 100%);
}

.project-header h3 {
  font-size: 24px;
  color: #FF8C00;
  margin: 0;
  font-weight: 700;
}

.project-description {
  color: #E0E0E0;
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 25px;
  flex-grow: 1;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 25px;
}

.tag {
  display: inline-block;
  padding: 6px 14px;
  border: 2px solid #FF8C00;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: all 0.3s ease;
  color: #FFA500;
  background: transparent;
}

.tag:hover {
  background: #FF8C00;
  color: #051821;
}

.project-links {
  display: flex;
  gap: 15px;
  margin-top: auto;
  padding-top: 20px;
  border-top: 1px solid #FF8C00;
}

.project-link {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
  font-size: 14px;
  color: #FF8C00;
}

.project-link:hover {
  transform: translateX(5px);
  color: #FFA500;
}

@media (max-width: 768px) {
  .projects {
    padding: 60px 20px;
  }

  .projects h2 {
    font-size: 36px;
    margin-bottom: 40px;
  }

  .projects-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .project-card {
    padding: 30px;
  }

  .project-header {
    gap: 15px;
  }

  .project-icon {
    width: 50px;
    height: 50px;
    font-size: 28px;
  }

  .project-header h3 {
    font-size: 20px;
  }
}

@media (max-width: 480px) {
  .projects h2 {
    font-size: 28px;
  }

  .project-card {
    padding: 20px;
  }

  .project-header h3 {
    font-size: 18px;
  }

  .project-description {
    font-size: 14px;
  }

  .tag {
    font-size: 10px;
    padding: 4px 10px;
  }
}
