.header {
  position: sticky;
  top: 0;
  z-index: 100;
  background: rgba(5, 24, 33, 0.95);
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0);
  border-bottom: 2px solid #FF8C00;
}

.header.scrolled {
  box-shadow: 0 2px 20px rgba(255, 140, 0, 0.2);
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  height: 70px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.logo {
  font-size: 28px;
  font-weight: bold;
  background: linear-gradient(135deg, #FF8C00 0%, #FFA500 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  cursor: pointer;
}

.nav-desktop {
  display: flex;
  gap: 40px;
  align-items: center;
}

.nav-desktop a {
  text-decoration: none;
  color: white;
  font-weight: 500;
  position: relative;
  transition: all 0.3s ease;
}

.nav-desktop a::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 2px;
  background: #FF8C00;
  transition: width 0.3s ease;
}

.nav-desktop a:hover {
  color: #FF8C00;
}

.nav-desktop a:hover::after {
  width: 100%;
}

.menu-toggle {
  display: none;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: white;
}

.nav-mobile {
  display: none;
  flex-direction: column;
  gap: 15px;
  padding: 20px;
  background: rgba(5, 24, 33, 0.98);
}

.nav-mobile a {
  text-decoration: none;
  color: white;
  font-weight: 500;
  padding: 10px 0;
  border-bottom: 1px solid #FF8C00;
  transition: color 0.3s ease;
}

.nav-mobile a:last-child {
  border-bottom: none;
}

.nav-mobile a:hover {
  color: #FFA500;
}

@media (max-width: 768px) {
  .nav-desktop {
    display: none;
  }

  .menu-toggle {
    display: block;
  }

  .nav-mobile {
    display: flex;
    position: absolute;
    top: 70px;
    right: 0;
    width: 100%;
    max-width: 300px;
    border-left: 1px solid #FF8C00;
  }

  .header-content {
    padding: 15px 20px;
    height: auto;
  }
}

@media (max-width: 480px) {
  .logo {
    font-size: 20px;
  }

  .nav-mobile {
    max-width: 100%;
  }
}
