.footer {
  padding: 40px 20px;
  background: #051821;
  color: white;
  text-align: center;
  border-top: 2px solid #FF8C00;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
}

.footer-content p {
  margin: 8px 0;
  font-size: 14px;
  color: #E0E0E0;
}

.highlight {
  background: linear-gradient(135deg, #FF8C00 0%, #FFA500 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
}

.footer-year {
  font-size: 12px;
  color: #999;
}

.footer-tech {
  font-size: 12px;
  color: #666;
}

@media (max-width: 480px) {
  .footer {
    padding: 30px 15px;
  }

  .footer-content p {
    font-size: 12px;
  }
}
