.social {
  padding: 100px 20px;
  background: linear-gradient(135deg, #FF8C00 0%, #FFA500 50%, #FFD700 100%);
  position: relative;
  overflow: hidden;
  color: white;
}

.social h2 {
  font-size: 48px;
  text-align: center;
  margin-bottom: 20px;
  color: #051821;
  font-weight: 900;
}

.social-subtitle {
  font-size: 18px;
  text-align: center;
  margin-bottom: 60px;
  color: #051821;
  font-weight: 600;
}

.social-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto 60px;
}

.social-card {
  background: rgba(5, 24, 33, 0.1);
  backdrop-filter: blur(10px);
  padding: 30px;
  border-radius: 20px;
  border: 2px solid rgba(5, 24, 33, 0.3);
  text-decoration: none;
  color: #051821;
  text-align: center;
  transition: all 0.3s ease;
  cursor: pointer;
}

.social-card:hover {
  background: rgba(5, 24, 33, 0.2);
  border-color: rgba(5, 24, 33, 0.5);
  transform: translateY(-5px);
  box-shadow: 0 15px 30px rgba(5, 24, 33, 0.2);
}

.social-link-wrapper {
  text-decoration: none;
  color: #051821;
  display: block;
  width: 100%;
  height: 100%;
}

.social-icon {
  font-size: 48px;
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
  color: #051821;
}

.social-card h3 {
  font-size: 20px;
  margin-bottom: 10px;
  font-weight: 700;
  color: #051821;
}

.social-card p {
  font-size: 14px;
  color: rgba(5, 24, 33, 0.8);
  line-height: 1.5;
}

.contact-cta {
  text-align: center;
  background: rgba(5, 24, 33, 0.15);
  backdrop-filter: blur(10px);
  padding: 50px 40px;
  border-radius: 20px;
  border: 2px solid rgba(5, 24, 33, 0.3);
  max-width: 600px;
  margin: 0 auto;
}

.contact-cta h3 {
  font-size: 28px;
  margin-bottom: 15px;
  color: #051821;
  font-weight: 700;
}

.contact-cta p {
  font-size: 16px;
  margin-bottom: 30px;
  color: #051821;
  font-weight: 600;
}

.contact-btn {
  display: inline-block;
  padding: 15px 40px;
  background: #051821;
  color: #FF8C00;
  border: none;
  border-radius: 50px;
  font-weight: 700;
  font-size: 16px;
  cursor: pointer;
  text-decoration: none;
  transition: all 0.3s ease;
}

.contact-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.3);
  background: #0a2830;
}

@media (max-width: 768px) {
  .social {
    padding: 60px 20px;
  }

  .social h2 {
    font-size: 36px;
    margin-bottom: 15px;
  }

  .social-subtitle {
    font-size: 16px;
    margin-bottom: 40px;
  }

  .social-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    margin-bottom: 40px;
  }

  .social-card {
    padding: 20px;
  }

  .social-icon {
    font-size: 36px;
  }

  .social-card h3 {
    font-size: 16px;
  }

  .contact-cta {
    padding: 30px 25px;
  }

  .contact-cta h3 {
    font-size: 22px;
  }
}

@media (max-width: 480px) {
  .social h2 {
    font-size: 28px;
  }

  .social-grid {
    grid-template-columns: 1fr;
  }

  .social-card {
    padding: 15px;
  }

  .social-icon {
    font-size: 32px;
  }

  .social-card p {
    font-size: 12px;
  }

  .contact-cta {
    padding: 20px 15px;
  }

  .contact-cta h3 {
    font-size: 18px;
  }

  .contact-btn {
    padding: 12px 30px;
    font-size: 14px;
  }
}
